@mixin laptopUp {
    @media (min-width: 1100px) {
        @content;
    }
}
.editProfilePicture{
    // height: 100vh;
    display: flex;
    align-items: center;
    max-width: 1200px;
    margin: 0 auto;
}
.editPicture {
    height: 100vh;
    overflow: auto!important;
    .postDescription{
        background-color: #fff;
        flex: 1;
        padding: 20px;
        @include laptopUp{
            max-width: 400px;
        }
    }
    .postDescription{
        border-bottom-right-radius: 8px;
        border-bottom-left-radius: 8px;
        @include laptopUp{
            border-top-right-radius: 8px;
            border-bottom-right-radius: 8px;
            border-bottom-left-radius: 0;
        }
    }
}
.pictureInputContainer{
    display: flex;
    justify-content: center;
    flex-direction: column;
    max-width: 702px;
    margin: 0 auto;
    @include laptopUp{
        flex-direction: row;
        max-width: 100%;
        margin: 0;
    }
}
.changeTimelineInputWrapper,
.changeProfileInputWrapper{
    position: relative;
    overflow: auto;
    border: 2px solid rgba(255,255,255,0.4);
    padding: 20px;
    
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    @include laptopUp{
        border-right: 0;
        border-top-right-radius: 0;
        border-bottom-left-radius: 8px;
    }
}
.dragInstruction{
    margin: 10px 0;
    color: #b2b2b2;
}
.ChangeProfilePicture{
    margin-top: 60px;
}
.informationUpdate{
    margin: 20px auto 0;
    max-width: 250px;
}