.CommentContainer{
    display: flex;
    padding: 6px 0;
}
.userCommentImage{
    position: relative;
    flex: 0 0 30px;
    width: 30px;
    height: 30px;
    border-radius: 30px;
    overflow: hidden;
    margin-right: 12px;
    img{
        position: absolute;
    }
}
.commentMessageWrapper{
    background: #F4F6F8;
    border-radius: 8px;
    padding: 12px;
}
.commentOptions{
    display: flex;
    padding: 2px 12px 0;
}
.commentOptionStyle{
    cursor: pointer;
    color: #00AB55;
    margin-right: 10px;
    font-size: 12px;
    font-weight: 600;
}
.nameAndDateContainer{
    margin-right: auto;
}
.commentNameAndDateContainer{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 4px;
    margin-right: auto;
}
.commenterName{
    font-weight: bold;
}
.commentDate{
    font-size: 12px;
    color: #919EAB;
}
