@mixin laptopUp {
    @media (min-width: 1100px) {
        @content;
    }
}
@mixin tabletUp {
    @media (min-width: 768px) {
        @content;
    }
}
@mixin mobileUp {
    @media (min-width: 590px) {
        @content;
    }
}

.timeline-wrapper{
    position: relative;
    overflow: hidden;
    padding-top: 37.037%;
    max-width: 1600px;
    margin: 0 auto;
    background-color: #dbdbdb;
}
.timeline-img-container{
    position: absolute;
    top: 0;
    width: 100%;
}
.profile-image-container{
    height: 120px;
    width: 120px;
    overflow: hidden;
    border: 4px solid #fff;
    border-radius: 50%;
    position: relative;
    margin-top: -60px;
    @include laptopUp{
        height: 200px;
        width: 200px;
        margin-top: -100px;
    }
    @include mobileUp {
        height: 150px;
        width: 150px;
        margin-top: -75px;
        border: 8px solid #fff;
    }
    
    img{
        position: absolute;

    }
}
.profile-and-image-and-name-wrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 auto;
    width: 100%;
    max-width: 1010px;
    padding: 0 20px;
    margin-bottom: 12px;
    text-align: center;
    @include mobileUp {
        flex-direction: row;
        align-items: flex-start;
        text-align: left;
        padding: 0 40px;
    }
}
.name-and-description{
    padding: 10px 14px;
}
.profile-name{
    color:#007B55;
    margin: 0;
    font-size: 24px;
    @include mobileUp {
        font-size: 28px;
    }
}
.profile-description{
    color: #656565;
    font-size: 14px;
    @include mobileUp {
        font-size: 16px;
    }
}
.profile-content-wrapper{
    margin: 0 auto;
    width: 100%;
    max-width: 1010px; 
    padding: 0 20px;
    @include mobileUp {
        padding: 0 40px;
    }
}
.ideas-and-purpose-wrapper{
    border-top: 1px solid #c0c0c0;
    border-bottom: 1px solid #c0c0c0;
    padding: 28px 10px;
}
.idea{
    text-align: center;
    font-size: 24px;
    line-height: 1;
    color: #222;
    margin-bottom: 18px;
    @include mobileUp {
        font-size: 28px;
    }
}
.purpose{
    font-size: 14px;
    line-height: 1.5;
    text-align: center;
    color: #767676;
    width: 80%;
    max-width: 409px;
    margin: 0 auto;
    @include mobileUp {
        font-size: 18px;
    }
}
.optionsContainerAndinfo{
    display: flex;
    margin: 0 auto;
    width: 100%;
    max-width: 1010px;
    padding: 20px 0;
    flex-direction: column-reverse;
    @include mobileUp {
        padding: 20px;
    }
    @include tabletUp {
        flex-direction: row;
    }
}
.postList{
    width: 100%;
    max-width: 550px;
    margin: 0 auto;
}
.PostUploadTrigger{
    display: flex;
    align-items: center;
    margin-bottom: 17px;
    padding: 0 20px;
    @include mobileUp {
        padding: 0;
    }
}
.postTriggerText{
    font-size: 18px;
    color: #222;
    border-radius: 20px;
    width: 100%;
    background-color: #efefef;
    min-height: 40px;
    display: flex;
    align-items: center;
    padding: 0 20px;
    flex-grow: 1;
    box-shadow: 0px 0px 2px rgba(145, 158, 171, 0.2), 0px 12px 24px -4px rgba(145, 158, 171, 0.12);
    cursor: pointer;
    transition: all 300ms ease;
    &:hover{
        background-color: #F4F6F8;
        box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
    }
}