.editPicture .ImageActionsContainer{
    margin-top: 56px;
}
.imageEditorContainer{
    text-align: center;
    .inputWrapper{
        color: #fff;
        display: flex;
        align-items: center;
        justify-content: space-between;
        .fileName{
            display: block;
            text-overflow: ellipsis;
            overflow: hidden;
            max-height: 3.6em;
            line-height: 1.8em;
            max-width: 180px;
            font-size: 12px;
            letter-spacing: 0.2px;
        }
    }
    .changeIcon{
        display: block;
        width: 100%;
        height: auto;
        path{
            fill: #fff;
        }
    }
    [role="presentation"]{
        position: relative;
        &:before{
            display: flex;
            align-items: center;
            justify-content: center;
            content: "Drop image here";
            font-weight: 400;
            color: #fff;
            position: absolute;
            top: 0;
            bottom: 0;
            right: 0;
            left: 0;
            z-index: 1;
        }
    }
    canvas{
        position: relative;
        z-index: 2;
    }
}