.goalTitleContainer{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 10px;
}
.goalTitle{
    color: #B78103;
    letter-spacing: 0.5px;
    font-weight: 700;
    display: flex;
    align-items: center;
    svg{
        margin-right: 4px;
    }
}
.goalsWrapper{
    width: 100%;
    max-width: 600px;
    
    .goal{
        background-color: rgba(255, 193, 7, 0.16);
        color: #B78103;
        border-left-width: 3px;
        border-left-style: solid;
        border-left-color: #B78103;
        padding: 1px 0 0 3px;
        margin-bottom: 0;
        font-size: 12px;
        font-weight: bold;
        padding: 4px;
        cursor: pointer;
        transition: opacity 300ms ease;
        margin-right: auto;
        &.notStarted{
            color: rgb(99, 115, 129);
            background: rgba(99, 115, 129, 0.16);
            border-left-color: rgb(99, 115, 129);
        }
        &.hidden{
            opacity: 0.5;
        }
    }
}
.goalItemWrapper{
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 4px;
    background: #fff;
    transition: all 300ms ease;
    &:hover{
        background: #ededed;
    }
}