@mixin mobileUp {
    @media (min-width: 590px) {
        @content;
    }
}
.PostCardsContainer{
    padding: 20px;
    border: 1px solid #c4c4c4;
    border-radius: 0;
    box-shadow: 0px 0px 2px rgba(145, 158, 171, 0.2), 0px 12px 24px -4px rgba(145, 158, 171, 0.12);
    margin-bottom: 20px;
    @include mobileUp {
        border-radius: 8px;
    }
}
.cardHeader{
    display: flex;
    margin-bottom: 17px;
    .name{
        font-size: 16px;
        line-height: 1.2;
        font-weight: 700;
        color: #212B36;
    }
    .date{
        color: #454F5B;
    }
}
.post-feeling{
    font-weight: 400;
    color: #a0a0a0;
}
.userPostImage{
    position: relative;
    flex: 0 0 40px;
    width: 40px;
    height: 40px;
    border-radius: 40px;
    overflow: hidden;
    margin-right: 18px;
    img{
        position: absolute;
    }
}
.energyWrapper{
    display: flex;
    align-items: center;
    .ratingWrapper{
        margin-bottom: -6px;
    }
}
.energyLabel{
    margin-right: 5px;
}
.cardContent{
    margin-bottom: 10px;
    font-weight: 600;
    color: #212121;
}
.cardContentWrapper{
    margin-bottom: 20px;
}
.cardImageWrapper{
    position: relative;
    max-height: 400px;
    overflow: hidden;
    margin: 0 -20px;
}
.cardReactionAndActions{
    display: flex;
    align-items: center;
    padding: 4px 0;
}
.postActions{
    flex: 1;
}
.commentAndShare{
    margin-left: auto;
}
